@import "src/scss/_variables";

.Background {
  position: relative;
  width: 100%;

  &.h-screen {
    min-height: 100vh;
  }

  &.gradient {
    background: $primary;
    background: linear-gradient(42deg, $primary 0%, $success 100%);
  }

  &.grey100 {
    background: $grey100;
  }
}
